}
.type__button-color {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgb(78, 195, 84);
  opacity: 0.3;
  animation: color 6s linear infinite;
}
.type__select {
  margin: 0 0 0 auto;
  padding: 8px 22px;
  display: flex;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
