@keyframes color {
  from {
    margin-left: -250px;
    margin-right: 250px;
  }
  40% {
    margin-left: 0;
    margin-right: 0;
  }
  to {
    margin-left: 0;
    margin-right: 0;
    opacity: 0;
  }
}
@keyframes slow-underline {
  from {
    padding-bottom: 21px;
    border-bottom: 1px solid white;
  }
  to {
    padding-bottom: 17px;
    border-bottom: 5px solid white;
  }
}
