.switch {
  margin: 0;
  padding: 8px 16px 8px 12px;
  height: 39px;
  display: flex;
  box-sizing: border-box;
  align-items: center;
  border-radius: 20px;
  background-color: rgba(0, 0, 0, 0.15);
  transition: all 0.2s linear;
}
.switch:hover {
  background-color: rgba(0, 0, 0, 0.25);
}
.switch__checkbox {
  margin: 0;
  padding: 0;
  height: 0;
  width: 0;
  box-sizing: border-box;
  visibility: hidden;
}
.switch__label {
  width: 42px;
  height: 20px;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  border-radius: 15px;
  position: relative;
}
.switch__label::after {
  content: "";
  position: absolute;
  top: 4px;
  left: 4px;
  width: 12px;
  height: 12px;
  background: #fff;
  border-radius: 50%;
  transition: 0.3s;
}
.switch__checkbox:checked + .switch__label {
  background-color: #22A526;
}
.switch__checkbox:checked + .switch__label::after {
  left: calc(100% - 4px);
  transform: translateX(-100%);
}
.switch__span {
  margin: 0;
  padding: 0 0 0 12px;
  box-sizing: border-box;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.46;
  color: white;
}

