.slider {
  margin: 0;
  padding: 0;
  display: flex;
  gap: 20px;
}
.slider__value {
  margin: 0;
  padding: 0 10px;
  width: 80px;
  height: 36px;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 2px;
  color: #CCCCCC;
  font-size: 18px;
  font-weight: 500;
  outline: none;
  background-color: transparent;
}
.slider__value:focus {
  background-color: #424140;
}

/*progress support*/
.slider__track, .slider__progress {
  --thumb: 16px;
  --range: calc(var(--max) - var(--min));
  --ratio: calc((var(--value) - var(--min)) / var(--range));
  --sx: calc(0.5 * var(--thumb) + var(--ratio) * (100% - var(--thumb)));
}

